<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.ck010)
      }
      return Arr
    },
    tzNum: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.tzNum)
      }
      return Arr
    },
    trafficNum: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.trafficNum))
      }
      return Arr
    },
    xhNum: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.xhNum)
      }
      return Arr
    },
    unxhNum: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.unxhNum))
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取字段列表 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        legend: {
          data: ['台账数量', '违法违规台账数量', '已销号台账数量', '未销号台账数量'],
          top: 30,
          right: 60
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '',
            minInterval: 1,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            name: '',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: '台账数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(194,53,49)'
              }
            },
            data: this.tzNum,
            barCategoryGap: '35%'
          },
          {
            name: '违法违规台账数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(47,69,84)'
              }
            },
            yAxisIndex: 1,
            data: this.trafficNum,
            barCategoryGap: '35%'
          },
          {
            name: '已消耗台账数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(97,160,168)'
              }
            },
            yAxisIndex: 1,
            data: this.xhNum,
            barCategoryGap: '35%'
          },
          {
            name: '未销号台账数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(212,130,101)'
              }
            },
            yAxisIndex: 1,
            data: this.unxhNum,
            barCategoryGap: '35%'
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
