<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    legendData: function () {
      return ['农业用地', '居民点', '工矿用地', '采石场']
    },
    pidData: function () {
      let Arr = []
      let eco = 0
      for (let item of this.datas) {
        eco = eco + item.farmNum
      }
      Arr.push(eco)
      let eqs = 0
      for (let item of this.datas) {
        eqs = eqs + item.residentNum
      }
      Arr.push(eqs)
      let nbs = 0
      for (let item of this.datas) {
        nbs = nbs + item.industrialNum
      }
      Arr.push(nbs)
      let hum = 0
      for (let item of this.datas) {
        hum = hum + item.quarryNum
      }
      Arr.push(hum)
      return Arr
    },
    seriesData: function () {
      let Arr = []
      for (let i in this.legendData) {
        let obj = {
          name: this.legendData[i],
          value: this.pidData[i]
        }
        Arr.push(obj)
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取数据 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '各问题点活动设施类型占比',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 20,
          top: 50,
          data: this.legendData
        },
        series: [
          {
            type: 'pie',
            radius: '45%',
            center: ['50%', '60%'],
            data: this.seriesData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

