<template>
  <div id="analysis-wrapper">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: 100%" ref="getQuery"></QueryBox>
    </div>
    <div class="analysisBox">
      <div>
        统计维度：
        <RadioGroup type="button" v-model="analysisType" @on-change="handleChangeType">
          <Radio label="ck010">活动/设施名称</Radio>
          <Radio label="cn010">整改进展</Radio>
          <Radio label="ck012">活动/设施现状</Radio>
        </RadioGroup>
      </div>
      <p>
        <span>问题点总数：<i>{{allChecked}}</i></span>
        <span>已核查数：<i>{{hasChecked}}</i></span>
        <span>未核查数：<i>{{haveNoChecked}}</i></span>
        <span>当日核查数：<i>{{todayChecked}}</i></span>
        <span>新增问题点数量：<i>{{newChecked}}</i></span>
      </p>
    </div>
    <div class="statistical">
      <transition name="fade">
        <div class="sta_box" :class="{activeBox: boxstate === 1}" style="left:0;top:0;padding: 15px 5px 5px 15px">
          <div class="con_box">
            <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 :1" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 1}">
              <div class="smallBox">
                <BarChart :data="getTableListData" v-if="analysisType === 'ck010'"></BarChart>
                <BarChart2 :data="getTableListData" v-else-if="analysisType === 'cn010'"></BarChart2>
                <BarChart3 :data="getTableListData" v-else-if="analysisType === 'ck012'"></BarChart3>
              </div>
            </div>
            <Spin size="large" fix v-if="spinShow1"></Spin>
          </div>
        </div>
      </transition>
      <div class="sta_box" :class="{activeBox: boxstate === 2}" style="left:50%;top:0;padding:15px 15px 5px 5px">
        <div class="con_box">
          <i class="stateTitle stateTitle3"></i>
          <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="handleTaleBox" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 2}">
            <div class="smallBox">
              <TableList :data="getTableListData" v-if="analysisType === 'ck010'" :heightStatus="heightStatus"></TableList>
              <TableList2 :data="getTableListData" v-else-if="analysisType === 'cn010'" :heightStatus="heightStatus"></TableList2>
              <TableList3 :data="getTableListData" v-else-if="analysisType === 'ck012'" :heightStatus="heightStatus"></TableList3>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow2"></Spin>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 3}" style="left:0;top:50%;padding: 5px 5px 15px 15px">
        <div class="con_box">
          <Button :icon="boxstate === 3 ? 'md-contract' : 'md-expand'" @click="mapOpen" size="small" class="boxBtn"></Button>
          <Map :boxstate="boxstate" ref="maps" @layerConClick="layerConClick"></Map>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 4}" style="left:50%;top:50%;padding: 5px 15px 15px 5px">
        <div class="con_box">
          <Button :icon="boxstate === 4 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 4 ? 0 :4" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 4}">
            <div class="smallBox">
              <PieChart :data="getPieData"></PieChart>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow3"></Spin>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'

import BarChart from './BarChart.vue'
import BarChart2 from './BarChart2.vue'
import BarChart3 from './BarChart3.vue'
import TableList from './TableList.vue'
import TableList2 from './TableList2.vue'
import TableList3 from './TableList3.vue'
import PieChart from './PieChart.vue'
import Map from './OnePiece'
import {
  // 任务列表列表
  listTaskSelect,
  // 统计数据
  listLedger
} from '@/api/dataAnalysis'
export default {
  name: 'dataAnalysis',
  data () {
    return {
      // 统计维度
      analysisType: 'ck010',
      // 任务列表
      taskList: [],
      /* 放大块的标识 */
      boxstate: 0,
      // 柱状图数据
      barChartData: {},
      // 表格数据
      getTableListData: [],
      getPieData: [],
      hasChecked: null, // 已核查
      haveNoChecked: null, // 未核查
      todayChecked: null, // 当日核查
      newChecked: null, // 新增核查
      allChecked: null, // 所有问题点核查
      spinShow1: true,
      spinShow2: true,
      spinShow3: true,
      // 放大缩小状态
      heightStatus: 'small'
    }
  },
  components: {
    QueryBox,
    BarChart,
    BarChart2,
    BarChart3,
    TableList,
    TableList2,
    TableList3,
    PieChart,
    Map
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 2,
          field: 'taskYear',
          title: '年份',
          data: [
            {
              label: '2017',
              value: '2017'
            },
            {
              label: '2018',
              value: '2018'
            },
            {
              label: '2019',
              value: '2019'
            },
            {
              label: '2020',
              value: '2020'
            },
            {
              label: '2021',
              value: '2021'
            },
            {
              label: '2022',
              value: '2022'
            }
          ]
        },
        {
          type: 2,
          field: 'cl001',
          title: '核查任务',
          data: this.taskList
        }
      ]
    }
  },
  created () {
    this.getTasks()
    this.getTableList()
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = {
        st4ScsCl: {
          cl001: data.cl001
        },
        taskYear: data.taskYear
      }
      this.spinShow1 = true
      this.spinShow2 = true
      this.spinShow3 = true
      this.getTableList()
    },
    // 获取核查任务列表
    getTasks () {
      listTaskSelect({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.taskList.push({
              label: item.cl002,
              value: item.cl001
            })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取统计数据 */
    getTableList () {
      listLedger({
        data: {
          groupByName: this.analysisType,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.allChecked = null
          this.hasChecked = null
          this.haveNoChecked = null
          this.todayChecked = null
          this.newChecked = null
          this.getTableListData = []
          this.getPieData = []
          res.data.data.forEach((item, index) => {
            this.allChecked += item.checkNum
            this.hasChecked += item.checkedNum
            this.haveNoChecked += item.uncheckNum
            this.todayChecked += item.todayNum
            this.newChecked += item.addNum
            // 根据统计维度值显示表格数据
            if (this.analysisType === 'ck010') {
              this.spinShow1 = false
              this.spinShow2 = false
              this.getTableListData.push({
                ck010: item.ck010,
                tzNum: item.tzNum,
                trafficNum: item.trafficNum,
                xhNum: item.xhNum,
                unxhNum: item.unxhNum
              })
            } else if (this.analysisType === 'cn010') {
              this.spinShow1 = false
              this.spinShow2 = false
              this.getTableListData.push({
                ck043: item.ck043,
                tzNum: item.tzNum,
                trafficNum: item.trafficNum,
                xhNum: item.xhNum,
                unxhNum: item.unxhNum
              })
            } if (this.analysisType === 'ck012') {
              this.spinShow1 = false
              this.spinShow2 = false
              this.getTableListData.push({
                ck012: item.ck012,
                tzNum: item.tzNum,
                trafficNum: item.trafficNum,
                xhNum: item.xhNum,
                unxhNum: item.unxhNum
              })
            }
            // 圆形统计图数据
            this.spinShow3 = false
            this.getPieData.push({
              farmNum: item.farmNum,
              residentNum: item.residentNum,
              industrialNum: item.industrialNum,
              quarryNum: item.quarryNum
            })
          })
          if (res.data.data.length === 0) {
            this.spinShow1 = false
            this.spinShow2 = false
            this.spinShow3 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    handleChangeType (val) {
      this.spinShow1 = true
      this.spinShow2 = true
      this.spinShow3 = true
      this.getTableList()
    },
    /* 地图框放大缩小开关控制 */
    mapOpen () {
      this.boxstate = this.boxstate === 3 ? 0 : 3
    },
    /* 点击图层控制器打开地图框 */
    layerConClick () {
      this.boxstate = 3
    },
    handleTaleBox () {
      if (this.boxstate === 2) {
        this.boxstate = 0
        this.heightStatus = 'small'
      } else {
        // 放大
        this.boxstate = 2
        this.heightStatus = 'big'
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #analysis-wrapper{
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .analysisBox {
      text-align: left;
      padding: 0 15px;
      font-size: 14px;
      overflow: hidden;
      &>div {
        float: left;
        .ivu-radio-group {
          .ivu-radio-wrapper {
            margin-left: 10px;
            border-radius: 4px;
            &:first-child {
              margin-left: 0;
            }
            &.ivu-radio-wrapper-checked {
              background: #2d8cf0;
              color: #fff;
            }
          }
        }
      }
      &>p {
        float: right;
        span {
          margin-right: 20px;
          i {
            color: #ff0000;
            font-style: normal;
            font-size: 16px;
            font-weight: bold;
          }
        }
      }
    }
    .statistical{
      position: relative;
      width: 100%;
      height:calc(100% - 95px);
      .sta_box{
        position: absolute;
        height: 50%;
        width: 50%;
        padding:15px 20px;
        .con_box{
          height: 100%;
          width: 100%;
          background: #fff;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          overflow: hidden;
          .boxBtn{
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:calc(100vw - 60px);
            height:100%;
            .smallBox{
              width:50%;
              height:100%;
              float: left;
            }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
          }
        }
      }
      /* box放大 */
      .activeBox{
        left:0!important;
        top:0!important;
        padding: 15px !important;
        width: 100%;
        height: 100%;
        z-index: 99;
        transition: all 0.8s ease;
      }
    }
  }
</style>
