<template>
  <div>
    <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
    </Table>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 326) / 2),
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '整改进展',
          align: 'center',
          key: 'ck043',
          ellipsis: true,
          tooltip: true,
          minWidth: 120
        },
        {
          title: '台账数量',
          align: 'center',
          key: 'tzNum',
          ellipsis: true,
          tooltip: true,
          minWidth: 90
        },
        {
          title: '违法违规台账数量',
          align: 'center',
          key: 'trafficNum',
          ellipsis: true,
          tooltip: true,
          minWidth: 140
        },
        {
          title: '已消耗台账数量',
          align: 'center',
          key: 'xhNum',
          ellipsis: true,
          tooltip: true,
          minWidth: 140
        },
        {
          title: '未销号台账数量',
          align: 'center',
          key: 'unxhNum',
          ellipsis: true,
          tooltip: true,
          minWidth: 140
        }
      ],
      data1: [],
      /* 加载 */
      loading: false
    }
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.resizeHeight()
    }, false)
  },
  props: {
    /* 编辑行原数据 */
    data: {},
    heightStatus: {}
  },
  watch: {
    data () {
      this.dataHandle()
    },
    heightStatus () {
      this.resizeHeight()
    }
  },
  methods: {
    /* 获取字段列表 */
    dataHandle () {
      this.data1 = this.data
    },
    // 当放大窗口table自适应
    resizeHeight () {
      if (this.heightStatus === 'big') {
        this.tableHeight = window.innerHeight - 332
        this.dataHandle()
      } else if (this.heightStatus === 'small') {
        this.tableHeight = Math.floor((window.innerHeight - 326) / 2)
        this.dataHandle()
      }
    }
  }
}
</script>

